
html, body {
  margin: 0;
  padding: 0;
  background-color: #fff;
}
#app {
  font-family: 'Roboto', Helvetica, Arial, sans-serif;
  font-variant-numeric: tabular-nums;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  padding: 0;
  margin: 0;
}
h1 {
  margin: 0;
}
.ui__subheader {
  margin: 20px 0 40px;
}
.ui__mainText {
}
.ui__formTitle {
  margin: 0 0 10px;
  font-weight: 600;
}
.ui__huge {
  font-size: 32px;
  font-weight: 900;
}
.calculatorSections {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-flow: row wrap;
          flex-flow: row wrap;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}
.calculatorSectionLeft {
  width: 40%;
}
.calculatorSectionRight {
  width: 55%;
}
.calculatorSection {
  margin-bottom: 20px;
}
.calculatorSection label {
  display: block;
  margin: 0 0 5px;
}
.calculatorSection input {
  display: inline-block;
  vertical-align: middle;
  margin: -2px 5px 0 0;
}
.calculatorSectionSummary {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-flow: row wrap;
          flex-flow: row wrap;
  -webkit-box-pack: start;
      -ms-flex-pack: start;
          justify-content: flex-start;
  margin: 20px 0 0 0;
}
.summaryValue {
  width: 200px;
  margin: 0 0 20px 0;
}
@media (max-width: 480px) {
.calculatorSections {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-flow: column;
            flex-flow: column;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
}
.calculatorSectionRight, .calculatorSectionLeft {
    width: 100%;
}
}
































































































































































